
.bubbleContainer{
  position: relative;
}
.bubble{
  padding: 20px;
  text-align: center;
  border-radius: 300px;
  display: -ms-flexbox;
  display: flex;
  font-weight: bold;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  position: absolute;
  -webkit-animation: moveX 7.05s linear 0s infinite alternate, moveY 7.4s linear 0s infinite alternate;
          animation: moveX 7.05s linear 0s infinite alternate, moveY 7.4s linear 0s infinite alternate;
  -webkit-box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
          box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
}
.bubble:after {
    background: -webkit-radial-gradient(center,  ellipse,  rgba(255,255,255,0.5) 0%,rgba(255,255,255,0) 70%);
    background: -o-radial-gradient(center,  ellipse,  rgba(255,255,255,0.5) 0%,rgba(255,255,255,0) 70%);
    background: radial-gradient(ellipse at center,  rgba(255,255,255,0.5) 0%,rgba(255,255,255,0) 70%); /* W3C */
  	border-radius: 50%;
  	-webkit-box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
  	        box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
    content: "";
    height: 90%;
    left: 10px;
    position: absolute;
    width: 90%;
}
.bubble span{
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

@-webkit-keyframes moveX {
  from { left: 0; } to { left: 1400px; }
}
@keyframes moveX {
  from { left: 0; } to { left: 1400px; }
}
@-webkit-keyframes moveY {
  from { top: 0; } to { top: 1000px; }
}
@keyframes moveY {
  from { top: 0; } to { top: 1000px; }
}



